@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Serif+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
body {
    margin: 0;
    padding: 0;
    font-family:'Roboto Slab', serif, sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

:root {
    --box-shadow: .2rem .3rem .5rem #00000033;
    --box-shadow-light: .2rem .3rem .5rem #00000022;
    --box-shadow-heavy: .4rem .6rem 1rem #00000055;
    --border-radius-rounded: 1em;
    --border-radius-pill: 100vw;
    --border-radius: .2em;
    --bounce: cubic-bezier(0.25, 1.85, 0.6, 1);

}

:root.dark {
    --white: #000000;
    --black: #ffffff;
    --off-black: #e8f3ff;
    --off-white: #17191a;
    --light-color: #145DA0;
    --light-color2: #144064;
    --light-color3: #050A30;
    --dark-color: #D4F1F4;
    --dark-color2: #75E6DA;
    --dark-color3: #189AB4;
    --danger-color: #dd4d4d;
    --danger-color2: #be3535;
}

:root.light {
    --white: #ffffff;
    --black: #000000;
    --off-white: #e8f3ff;
    --off-black: #17191a;
    --dark-color: #050A30;
    --dark-color2: #144064;
    --dark-color3: #145DA0;
    --light-color: #D4F1F4;
    --light-color2: #75E6DA;
    --light-color3: #189AB4;
    --danger-color: #dd4d4d;
    --danger-color2: #be3535;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

::-webkit-scrollbar {
    width: .8em;
    height: .8em;
}

::-webkit-scrollbar-track {
    background: var(--off-white);
}

::-webkit-scrollbar-thumb {
    background: var(--dark-color2);
    border-radius: 1em;
    border: var(--off-white) solid 2px;
    box-sizing: border-box;
}

::-webkit-scrollbar-thumb:hover {
    background: var(--light-color);
}

@supports(scrollbar-width: none) {
    * {
        scrollbar-color: var(--light-color2) var(--dark-color2);
        scrollbar-width: thin;
    }
}

@media (prefers-reduced-motion: reduce) {
    * {
        opacity: 1 !important;
    }

    *,
    ::after,
    ::before {
        animation: none !important;
        transition: all 0ms !important;
    }

}

a {
    text-decoration: none;
    color: var(--dark-color2);
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
a {
    position: relative;
}

p {
    text-align: justify;
}

a:focus,
a:hover {
    color: var(--dark-color3);
}

h1,
h2,
h3,
h4,
h5,
h6{
    font-family:  'Source Serif Pro', serif;
}
body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: var(--off-white);
    color: var(--dark-color);
    transition: color 250ms, background-color 500ms;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}
@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.app {
    opacity: 0;
    height: 100vh;
    padding: 1rem;
    overflow: auto;
    animation: fadeIn 500ms forwards;
    font-size: 14pt;
}

.container {
    position: relative;
    max-width: 1080px;
    margin: auto;
    padding: 2rem;
    display: flex;
}


.profile-pic {
    display: inline;
    max-width: 300px;
    max-height: 300px;
    overflow: hidden;
    border-radius: var(--border-radius-pill);
    flex: 1 1 300px;
}

.profile-pic img {
    max-width: 300px;
    max-height: 300px;
    width: 100%;
    object-fit: cover;
    object-position: top;
}

.about-text {
    flex: 1 1 50%;
    text-align: justify;
}

.project-text {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: calc(100% - 500px - 4rem);
    min-width: 300px;
    text-align: justify;
}

video {
    border-radius: var(--border-radius);
    box-shadow: var(--box-shadow);
    max-width: 500px;
    margin: auto;
}


.card.click-card {
    position: relative;
}


.card.click-card:not(.active) {
    outline: solid 3px var(--light-color3);
}

.card.click-card:not(.active) .card-header {
    border: none;
}

.card.click-card .card-body ul {
    margin-top: 3rem;
    margin-left: 2rem;
}

.card.click-card .card-header h2 {
    z-index: 1;
    white-space: nowrap;
}

.card.click-card .level-wrapp {
    position: absolute;
    height: 100%;
    z-index: 0;
    inset: 0;
    margin: 0;
    transform-origin: top;
    transition: background-color 500ms, top 250ms, transform 250ms, height 250ms;
}

.card.click-card.active .level-wrapp {
    top: 6rem;
    height: 2rem;
    transform: scaleX(.9);
}

.line {
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.line:last-child {
    margin-bottom: 0rem;
}

.line:first-child {
    margin-top: 0rem;
}

@media (max-width: 991px) {
    .app{
        font-size: 12pt;
    }
}

@media (max-width: 556px) {
    .app{
        font-size: 10pt;
    }
}
.frame {
    position: relative;
    outline: solid 3px var(--dark-color2);
    border-radius: var(--border-radius);
    padding: 2rem;
    margin-top: 4rem;
    margin-bottom: 3rem;
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    flex-wrap: wrap;
    align-items: flex-start;
    box-sizing: border-box;
}

.frame .title {
    position: absolute;
    top: -1.5em;
    left: 1rem;
    background: var(--off-white);
    padding: 1rem;
    white-space: nowrap;
    transition: background-color 500ms;
}


.frame:focus {
    outline: solid 3px var(--dark-color3);
}

@media screen and (max-width: 640px) {
    .frame .title {
        font-size: 1.4rem;
    }
}
canvas {
    position: relative;
    border-radius: var(--border-radius);
    box-shadow: var(--box-shadow);
    max-height: 500px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}
.card {
    background-color: var(--white);
    box-shadow: var(--box-shadow-light);
    border-radius: var(--border-radius);
    min-width: 150px;
    overflow: hidden;
    transition: background-color 500ms;
}

.card-header:not(:last-child) {
    border-bottom: var(--off-black) solid 1px;
    padding: 1em;
}

.card-body:not(:last-child) {
    border-bottom: var(--off-black) solid 1px;
    padding: 1em;
}

.card-body.collapse {
    padding: 0;
    transition: opacity 250ms, height 250ms 250ms ease;
}

.active .card-body.collapse {
    transition: opacity 250ms, height 250ms ease;
}

.collapse-padding {
    padding: 1em;
}

.card-footer {
    padding: 1em;
}

.click-card {
    cursor: pointer;
    transition: transform 250ms var(--bounce);
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: calc(50% - 4rem);
}

.click-card:hover,
.click-card:focus {
    transform: scale(1.02);
    z-index: 1;
}
@keyframes fill {
    from {

        transform: scaleX(0);
    }

    to {
        transform: scaleX(1);
    }
}

.level-wrapp {
    position: relative;
    width: 100%;
    height: 2rem;
    background-color: var(--light-color3);
    border-radius: var(--border-radius);
    overflow: hidden;
    margin-top: 1rem;
    outline: solid 2px var(--light-color3);
    transition: background-color 500ms;
}

.level-1 {
    position: absolute;
    inset: 0;
    background-color: var(--light-color2);
    transform: scaleX(0);
    transform-origin: left;
    transition: background-color 500ms;
}

.level-2 {
    position: absolute;
    inset: 0;
    background-color: var(--light-color);
    transform: scaleX(0);
    transform-origin: left;
    transition: background-color 500ms;
}

.level-1.anim {
    animation: fill 1s ease forwards;
}

.level-2.anim {
    animation: fill 1s 1s ease forwards;

}
.action-center {
    position: -webkit-sticky;
    position: sticky;
    display: block;
    height: 3rem;
    top: 0;
    z-index: 10;
}

.action-btn {
    width: 3rem;
    height: 3rem;
    border: none;
    background-color: var(--off-white);
    color: var(--off-black);
    border-radius: var(--border-radius-pill);
    box-shadow: var(--box-shadow-light);
}

.action-btn:focus,
.action-btn:hover {
    background-color: var(--off-black);
    color: var(--off-white);
}

.action-btn:focus span,
.action-btn:focus span::before,
.action-btn:focus span::after,
.action-btn:hover span,
.action-btn:hover span::before,
.action-btn:hover span::after {
    background-color: var(--off-white);
}

/* Three lines ---------------------------*/
.mobile-nav-toggle {
    display: flex;
    justify-content: center;
    position: absolute;
    z-index: 10;
}

.mobile-nav-toggle:hover {
    cursor: pointer;
}

.mobile-nav-toggle span,
.mobile-nav-toggle span::before,
.mobile-nav-toggle span::after {
    position: absolute;
    top: .7rem;
    width: 2rem;
    height: 5px;
    border-radius: 2px;
    background: var(--dark-color);
    transition: transform 400ms var(--bounce),
        opacity 200ms;
}

.mobile-nav-toggle span::before,
.mobile-nav-toggle span::after {
    content: '';
    left: 0rem;
}

/* -------------------------------------- */
/* transforming between X and three lines */
.mobile-nav-toggle.shown span {
    transform:
        translateY(0.8em) rotate(45deg);
}

.mobile-nav-toggle.shown span::before {
    transform:
        translateY(-0.8em) rotate(-90deg);
}

.mobile-nav-toggle.shown span::after {
    transition: opacity 50ms;
    opacity: 0;
}

.mobile-nav-toggle span::before {
    top: .8em;
}

.mobile-nav-toggle span::after {
    top: 1.6em;
}

.theme-btn {
    z-index: 9;
    position: absolute;
    cursor: pointer;
    pointer-events: none;
    transition: color 100ms,
        background-color 100ms,
        transform 400ms var(--bounce);
}

.theme-btn svg {
    width: 100%;
    height: 100%;
}

.theme-btn.shown {
    transform: translateX(3.5rem);
    pointer-events: unset;
}

.lang-btn {
    z-index: 8;
    position: absolute;
    cursor: pointer;
    pointer-events: none;
    transition: color 100ms,
        background-color 100ms,
        transform 400ms 50ms var(--bounce);
}

.lang-btn svg {
    width: 100%;
    height: 100%;
}

.lang-btn.shown {
    transform: translateX(7rem);
    pointer-events: unset;
}

.pdf-btn {
    z-index: 7;
    position: absolute;
    cursor: pointer;
    transition: color 100ms,
        background-color 100ms,
        transform 400ms 100ms var(--bounce);
}

.pdf-btn svg {
    width: 100%;
    height: 100%;
}

.pdf-btn.shown {
    transform: translateX(15rem);
}
